.menu_menuWrap__2vobF .menu_hamburger__1iMYp  > div{
	position: relative;
	width: 100%;
	height: 2px;
	background: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.4s ease;
}

.menu_menuWrap__2vobF .menu_hamburger__1iMYp  > div:before,
.menu_menuWrap__2vobF .menu_hamburger__1iMYp  > div:after{
	content: '';
	position: absolute;
	z-index: 3;
	top: -10px;
	width: 100%;
	height: 2px;
	background: #fff;
}
.menu_menuWrap__2vobF .menu_hamburger__1iMYp  > div:after{
	top: 10px;
}

.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked + .menu_hamburger__1iMYp > div{
	transform: rotate(135deg);
}
.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked + .menu_hamburger__1iMYp > div:before,
.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked + .menu_hamburger__1iMYp > div:after{
	top: 0;
	transform: rotate(90deg);
}

.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked:hover + .menu_hamburger__1iMYp > div{
	transform: rotate(225deg);
}

.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked ~ .menu_menu__3eQTx {
	visibility: visible;
}

.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked ~ .menu_menu__3eQTx >div {
	transform: scale(1);
	transition:0.75s;	
}

.menu_menuWrap__2vobF .menu_toggler__2Zvmd:checked ~ .menu_menu__3eQTx >div >div{
	opacity: 1;	
	transition: opacity 0.4s ease;
}

.menu_menuWrap__2vobF .menu_menu__3eQTx {
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	visibility: hidden;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
}
.menu_menuWrap__2vobF .menu_menu__3eQTx > div {
	background: #6F79FF;
	width: 100vw;
	height: 50vw;
